@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'RoleyPoley';
  src: url(/static/media/RoleyPoley.1144a500.eot);
  src: url(/static/media/RoleyPoley.1144a500.eot?#iefix) format("embedded-opentype"),
      url(/static/media/RoleyPoley.44cfe986.woff) format("woff"),
      url(/static/media/RoleyPoley.5f510ef1.ttf) format("truetype"),
      url(/static/media/RoleyPoley.781b6ad5.svg#RoleyPoley) format("svg");
  font-weight: normal;
  font-style: normal;
}

html {
  font-size: 16px;
  line-height: 1.2;
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
}

body {
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-weight: 300;
}

h1 {
  margin: 0;
  font-family: 'RoleyPoley', Verdana, Arial, sans-serif;
  font-weight: 300;
  font-size: 4em;
  font-size: 3.75em;
}

h2, h3 {
  margin: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a:active {
  color: red;
}

.constraint {
  margin: 0 auto;
  max-width: 1200px;
  width: 90%;
}

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 4.75em;
  }
}

@media only screen and (min-width: 992px) {
  h1 {
    font-size: 5.625em;
  }
}

@media only screen and (min-width: 1200px) {
  
}


.animal-container-outer {
    border-radius: 2em;
    margin-top: 1.5em;
    margin-bottom: 1.5em;
    padding: 2em;
    background-color: #f1f1f1;
    border: 3px solid #e5640f;
}

.animal-container-outer:first-of-type {
    margin-top: 0;
}

.animal-container-outer:last-of-type {
    margin-bottom: 0;
}

.animal-container-inner {
    display: flex;
    flex-flow: column wrap;
    margin-top: 1em;
}

.animal-container__item {
    flex: 1 2 30%;
}

.animal-container__item h4, .animal-container__item p {
    margin: .5em 0;
}

.animal-container__item li {
    display: inline-block;
    margin-right: .5em;
}

.back-to-top {
    cursor: pointer;
    font-weight: bold;
    color: #FFF;
}

.back-to-top:hover, .back-to-top:focus {
    color: #e5640f;
}


@media only screen and (min-width: 920px) {
    .animal-container-inner {
        flex-flow: row wrap;
    }

    .animal-container-outer {
        margin-top: 0;
        margin-bottom: 3em;
    }

    .back-to-top {
        display: none;
    }
}
.image-gallery {
    padding: 2em;
    background: #DDD;
    margin-top: 1.5em;
}

.image-gallery-slides {
    border: 15px solid #FFF;
    border-radius: 1em;
}
.question-text {
    font-size: 1.5em;
}

.answer-section {
    margin: 0 auto;
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
    width: 400px;
    height: 400px;
}

.quiz-button {
    font-family: 'RoleyPoley', Verdana, Arial, sans-serif;
    font-size: 1.3em;
    border: 2px solid #e5640f;
    border-radius: 2em;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

.quiz-button:hover {
    cursor: pointer;
    color: #e5640f;
}

.quiz-button:active {
    box-shadow: inset 0 0 10px #000000;
}

#reset-button {
    width: 100px;
    height: 30px;
    font-family: 'RoleyPoley', Verdana, Arial, sans-serif;
    background-color: #1a3e66f8;
    color: #FFF;
    text-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

@media screen and (max-width: 480px) {
    .answer-section {
        margin: 0 auto;
        display: grid;
        grid-template-rows: repeat(4, 1fr);
        grid-template-columns: repeat(1, 1fr);
        width: 300px;
        height: 300px;
    }
}
.top-nav-bar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  #top-nav-bar-item {
    float: left;
  }
  
  #top-nav-bar-item a {
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    background: #DDD;
    border-bottom-left-radius: 1.5em;
    border-bottom-right-radius: 1.5em;
  }

  #top-nav-bar-item a:hover {
      color: #e5640f;
  }

  #top-nav-bar-item a.active{
    color: #e5640f;
    border-bottom: 5px solid #e5640f; 
    margin-bottom: 2em;
  }
.quiz-page {
    background: url(/static/media/bg-water-bubbles.661b1d98.jpeg) no-repeat center center fixed;
    background-size: cover;
    min-height: 100vh;
    margin: 0;
    font-size: 1.125em;
}

.quiz-container{
    text-align: center;
    color: #FFF;
}

#quiz-title {
    text-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}


.container-menu-jump-to {
    position: static;
    position: initial;
    border: 3px solid #e5640f;
    display: flex;
    flex-flow: column wrap;
    flex-basis: 100%;
    align-self: flex-start;
    vertical-align: top;
    background-color: #f1f1f1;
    color: #231f20;
    margin-bottom: 1em;
    padding: 1.5em 0 1.5em 1.5em;
    top: 24px;
    border-radius: 2em;
}

.menu-jump-to {
    padding-top: 1em;
}

.list-item, .quiz-link {
    cursor: pointer;
    padding: 0.5em 0;
    font-weight: bold;
    text-decoration: underline;
}

.quiz-link a {
    color: #231f20;
}

.list-item a:hover, .list-item a:focus, .quiz-link a:hover, .quiz-link a:focus {
    color: #e5640f;
}

.list-item a.active, .quiz-link a.active {
    border-left: 5px solid #e5640f; 
    padding-left: 0.5em;
}

.container-menu-jump-to h3 {
    font-weight: 300;
}


@media only screen and (min-width: 920px) {
    .container-menu-jump-to {
        position: -webkit-sticky;
        position: sticky;
        flex-basis: 17%;
    }
  }

.sea-animals {
  background: url(/static/media/bg-water-bubbles.661b1d98.jpeg) no-repeat center center fixed;
  background-size: cover;
  min-height: 100vh;
  margin: 0;
  font-size: 1.125em;
}

.content-container {
  display: block;
  flex-direction: column;
  justify-content: space-between;
}

.animal-wrapper {
  flex-basis: 78%;
}

@media only screen and (min-width: 920px) {
  .content-container {
    display: flex;
    justify-content: space-between;
    flex-flow: row;
  }
}

.home {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: url(/static/media/bg-homepage-ocean-landscape.f2f7b010.jpg) no-repeat center center fixed;
    background-size: cover;
    min-height: 100vh;
    margin: 0;
    color: #FFF;
}

.home h1 {
    text-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

.nav-bar-container-home {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 2em;
}

.nav-bar-container-home ul {
    width: auto;
    display: flex;
    justify-content: space-between;
    flex-flow: column wrap;
}

.nav-bar-container-home li {
    display: inline-flex;
    flex-flow: column wrap;
    text-transform: uppercase;
    margin-bottom: 1.5em;
    color: #888;
    background-color: #fff;
    justify-content: center;
    align-items: center;
    border-radius: 2em;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}

.nav-bar-container-home li:first-of-type {
    display: none;
}

.nav-bar-container-home li a {
    padding: .5em 1.5em 1.5em 1.5em;
    width: 160px;
    text-decoration: none;
    color: #000;
    font-weight: 500;
    font-size: 1.25em;
}

.nav-bar-container-home li:hover, .nav-bar-container-home li:focus {
    transform: scale3d(1.025, 1.025, 1.025);
}

.nav-bar-container-home ul li::before {
    display: inline-block;
    content: "";
    width: 100px;
    height: 100px;
    margin: 2em 0;
}

.nav-bar-container-home ul li:nth-child(2)::before {
    background: url(/static/media/icon-whale.f1638497.svg) no-repeat center center;
}

.nav-bar-container-home ul li:nth-child(3)::before {
    background: url(/static/media/icon-quiz.daa8fd4e.svg) no-repeat center center;
}

.nav-bar-container-home ul li:nth-child(4)::before {
    background: url(/static/media/icon-map.883e6f6e.svg) no-repeat center center;
}

@media only screen and (min-width: 768px) {

}

@media only screen and (min-width: 920px) {
    .nav-bar-container-home ul {
        flex-flow: row wrap;
        width: 100%;
        max-width: 1000px;
    }

    .nav-bar-container-home li {
        margin-bottom: 2em;
    }

    .nav-bar-container-home ul li::before {
        width: 120px;
        height: 120px;
    }

    .nav-bar-container-home li a {
        padding: .5em 2em 1.5em 2em;
        width: 180px;
        font-size: 1.2em;
    }
}

.leaflet-container {
    height: 100vh;
	width: 100vw;
    background-color: white;
    display: block;
    margin: auto;
    border-radius: 2em;
    border: 3px solid #e5640f;
}

#map {
    height: 100vh;
	width: 100vw;
}

.map-page{
background: url(/static/media/bg-water-bubbles.661b1d98.jpeg) no-repeat center center fixed;
  background-size: cover;
  min-height: 100vh;
  margin: 0;
  font-size: 1.125em
}

